.about {
  &__title {
    margin: 0 0 32px;
    font-weight: 500;
    font-size: 32px;
    line-height: 100%;

    letter-spacing: -1.5px;

    @include onTablet {
      margin-bottom: 0;
      font-size: 48px;
    }

    @include onDesctop {
      font-size: 64px;
    }
  }

  &__paragraph {
    font-weight: 500;
    font-size: 23px;
    line-height: 140%;
  }
}
